<template>
  <v-container>
    <v-data-table
      :headers="headers"
      :items="mascotas"
      sort-by="calories"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Tabla de Mascotas</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                New Item
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.nombre"
                        label="Nombre de la Mascota"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.especie"
                        label="Especie"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.raza"
                        label="Raza"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.edad"
                        label="Edad"
                      ></v-text-field>
                    </v-col>                    
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="editarMascota(editedItem)"> Modificar </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5"
                >Esta seguro que desea borrar esta mascota?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon small class="mr-2" @click="editItem(item._id)"> mdi-pencil </v-icon>
        <v-icon small @click="eliminarMascota(item._id)"> mdi-delete </v-icon>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize"> Reset </v-btn>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      { text: "ID de mascota", value: "_id" },
      { text: "Nombre de mascota", value: "nombre" },
      { text: "Especie", value: "especie" },
      { text: "Raza", value: "raza" },
      { text: "Edad", value: "edad" },
      { text: "Fecha de ingreso", value: "date" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    mascotas: [],
    editedIndex: -1,
    editedItem:[],
    defaultItem: {},
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
    this.listarMascota();
  },

  methods: {
    initialize() {

      ;
    },

    editItem(item) {
      this.dialog = true;
      console.log('Este es el id:',item);

      this.axios.get(`/buscar/${item}`)
       .then(res=>{
         console.log(res.data);
         this.editedItem = res.data
       })
    },

    deleteItem(item) {
      this.editedIndex = this.mascotas.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.mascotas.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.mascotas[this.editedIndex], this.editedItem);
      } else {
        this.mascotas.push(this.editedItem);
      }
      this.close();
    },

    listarMascota(){
      this.axios.get('/todos')
       .then((res)=>{
         this.mascotas = res.data;
       })
       .catch((e)=>{
         console.log('Error', e);
       })
    },

    editarMascota(item){
      this.axios.put(`/actualizar/${item._id}`, item)
       .then((res)=>{
         this.$swal({
              icon: 'success',
              title: 'Modificacion Exitosa',
              text: 'Datos de la Mascota Modificados!'
            })
         this.close();
         const index = this.mascotas.findIndex(n => n._id === res.data._id)
         this.mascotas[index].nombre = res.data.nombre;
         this.mascotas[index].especie = res.data.especie;
         this.mascotas[index].raza = res.data.raza;
         this.mascotas[index].edad = res.data.edad;
       })
       .catch((e)=>{
         console.log('Error', e);
       })
    },

    eliminarMascota(id){
      console.log(id)
      this.axios.delete(`/eliminar/${id}`)
       .then(res =>{
         this.$swal({
             icon: 'error',
             title: 'Eliminacion Exitosa',
             text: 'Mascota Eliminada!'
           })
         const index = this.mascotas.findIndex(item => item._id === res.data._id)
         this.mascotas.splice(index, 1);
         this.editedIndex = this.mascotas.indexOf(item);
         this.editedItem = Object.assign({}, item)
       }).catch(e=>{
         console.log(e.response)
       })
    }
  },
};
</script>